// Local Apps ---------------------
// Default Home Page
import Home from './apps/home/home';
// User Pages
import Users from './apps/users/list';
import UsersEdit from './apps/users/edit';
// Jobs Pages
import JobsList from './apps/jobs/list';
import JobsEdit from './apps/jobs/edit';
// Locals Apps error imports ------
import PageNotFoundError from './apps/404/404';
// Local imports ------------------
import { Route, Switch } from './shared/components/routes/routes';


// App routes
const Routes = () => {
  return <Switch>

    { // [HOME] Default Page
      Route({
        path: '',
        app: Home
      })
    }

    { // [USER] List Page
      Route({
        path: 'users',
        app: Users
      })}
    { //        View & Edit Page
      Route({
        path: 'users/:uid',
        app: UsersEdit
    })}

    { // [JOB]  List Page
      Route({
        path: 'jobs',
        app: JobsList
    })}
    { //        View & Edit Page
      Route({
        path: 'jobs/:uid',
        app: JobsEdit
    })}

    { // [404]  Page not found
      Route({
        path: '*',
        any: true,
        app: PageNotFoundError
    })}

  </Switch>
}

export default Routes;
